<template>

    <div class="agreement-login">

        <div class="h4"
             v-html="$t('agreement.title', {person: person.name})" />

        <p v-html="$t('agreement.descr', {company_name: person.company.name})" />

        <a href="#" class="link">Информация о кредите</a>

        <div class="agreement-login-btn">

            <button class="btn btn-sm btn-block"
                    target="_blank"
            @click.prevent="loginClick"
            :class="{'loading': load}"
            :disabled="load"
            v-html="$t('agreement.sign_btn_text')"/>

            <p v-html="$t('agreement.sign_footer_text')" />

        </div>

    </div>

</template>

<script>
    import {mapState} from "vuex";

    export default {
        name: "AgreementMain",
        computed: {
            ...mapState({
                person: state => state.agreement.person,
                esiaLink: state => state.agreement.esiaLink,
            }),
        },
        data: () => ({
            getParametr: '',
            load: false
        }),
        methods: {
            loginClick() {
                this.load = true
                this.$store.dispatch('signInEsia', this.getParametr).then(() => {
                    this.load = false
                })
            }
        },
        async mounted() {
            const params = window
                .location
                .search
                .replace('?','')
                .split('&')
                .reduce(
                    function(p,e){
                        const a = e.split('=');
                        p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                        return p;
                    },
                    {}
                );
            this.getParametr = params['c']
            await this.$store.dispatch('clearCookie', this.getParametr)
            this.$store.dispatch('getPersonData', this.getParametr)
        }
    }
</script>

